/* src/App.css */
@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@400;600;700&display=swap');

:root {
  --primary-bg: #1a1a2e;
  --secondary-bg: #16213e;
  --card-bg: #2a2a4a;
  --text-primary: #ffffff;
  --text-secondary: #cccccc;
  --accent: #00aaff;
  --error: #ff5555;
  --button-hover: #0088cc;
  --shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Montserrat', sans-serif;
  background: linear-gradient(135deg, var(--primary-bg) 0%, var(--secondary-bg) 100%);
  color: var(--text-primary);
  min-height: 100vh;
  padding: 20px;
  text-align: center;
}

.app-container {
  max-width: 100%;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.header {
  font-size: 2.5rem;
  font-weight: 700;
  color: var(--accent);
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
  margin-bottom: 15px;
}

.mobile-notice {
  font-size: 0.9rem;
  color: var(--error);
  margin-bottom: 15px;
}

.account-info {
  font-size: 1rem;
  color: var(--text-secondary);
  margin-bottom: 20px;
  word-break: break-all;
}

.error-message {
  font-size: 0.9rem;
  color: var(--error);
  margin-bottom: 15px;
}

.section-title {
  font-size: 1.8rem;
  color: var(--accent);
  margin-bottom: 20px;
}

.nft-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 15px;
  width: 100%;
  max-width: 1200px;
}

.experience-section {
  margin-top: 30px;
  width: 100%;
  max-width: 1200px;
}

.button {
  padding: 12px 25px;
  font-size: 1rem;
  font-weight: 600;
  color: var(--text-primary);
  border: none;
  border-radius: 25px;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.2s ease;
  box-shadow: var(--shadow);
  width: 100%;
  max-width: 300px;
  margin-bottom: 20px;
}

.connect-button {
  background-color: var(--accent);
}

.connect-button:hover {
  background-color: var(--button-hover);
  transform: scale(1.05);
}

.disconnect-button {
  background-color: var(--error);
}

.disconnect-button:hover {
  background-color: #cc4444;
  transform: scale(1.05);
}

.nft-card {
  background-color: var(--card-bg);
  border-radius: 15px;
  padding: 15px;
  box-shadow: var(--shadow);
  transition: transform 0.3s ease;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.nft-card:hover {
  transform: scale(1.05);
}

.nft-image-container {
  width: 100%;
  max-height: 200px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 10px;
  overflow: hidden;
}

.nft-image {
  width: 100%;
  height: auto;
  max-height: 200px;
  object-fit: contain;
  border-radius: 10px;
}

.nft-title {
  font-size: 1.1rem;
  margin-bottom: 8px;
  color: var(--text-primary);
}

.nft-detail {
  font-size: 0.85rem;
  color: var(--text-secondary);
  margin-bottom: 5px;
}

.nft-token-id {
  font-size: 0.8rem;
  color: #999999;
  word-break: break-all;
}

.experience-card {
  background-color: var(--card-bg);
  border-radius: 15px;
  padding: 15px;
  margin-bottom: 15px;
  box-shadow: var(--shadow);
}

.experience-text {
  font-size: 0.9rem;
  color: var(--text-secondary);
  margin-bottom: 10px;
}

.event-button {
  background-color: var(--accent);
  padding: 10px 20px;
  font-size: 0.9rem;
  max-width: 250px;
}

.event-button:hover {
  background-color: var(--button-hover);
  transform: scale(1.05);
}

/* Media Queries para m√≥viles */
@media (max-width: 768px) {
  .header {
    font-size: 2rem;
  }

  .section-title {
    font-size: 1.5rem;
  }

  .nft-grid {
    grid-template-columns: 1fr;
    gap: 10px;
  }

  body {
    padding: 15px;
  }

  .button {
    padding: 10px 20px;
    font-size: 0.9rem;
  }

  .nft-card {
    padding: 10px;
  }

  .nft-image-container {
    max-height: 180px;
  }

  .nft-title {
    font-size: 1rem;
  }

  .nft-detail {
    font-size: 0.8rem;
  }

  .nft-token-id {
    font-size: 0.75rem;
  }

  .experience-card {
    padding: 10px;
  }

  .experience-text {
    font-size: 0.85rem;
  }

  .event-button {
    font-size: 0.85rem;
    padding: 8px 15px;
  }
}